
import React, { useState } from 'react';
import '../../../shared/CSS/FormStyles.css';
import styles from './task.module.css';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import _ from "lodash"; /* npm lodash */
import {v4} from "uuid"; /* npm uuid*/

const item = {
	id: v4(),
	name: "Task Title: Adding Design ",
    name2: "Due Date: 1-June-2021",
	name3: "Priority: Low"
}
const item2 = {
	id: v4(),
	name: "Task Title: Create Task page",
    name2: "Due Date: 30-May-2021",
	name3: "Priority: High"
}
const item3 = {
	id: v4(),
	name: "Task Title: Route Pages",
    name2: "Due Date: 5-July-2021 ",
	name3: "Priority: Low"
}
const item4 = {
	id: v4(),
	name: "Task Title: Database Create ",
    name2: "Due Date: 3-Augest-2021",
	name3: "Priority: High"
}
const item5 = {
	id: v4(),
	name: "Task Title: Delete Comments",
    name2: "Due Date: 10-September-2021",
	name3: "Priority: Low"
}
const item6 = {
	id: v4(),
	name: "Task Title: Project Testing",
    name2: "Due Date: 31-December-2021",
	name3: "Priority: High"
}

const Task = () => {
	
	const [state, setState] = useState({
		"todo": {
			title: "To do",
			items: [item, item2, item3]
		},
		"in-progress": {
			title: "In Development",
			items: [item4]
		},
		"done": {
			title: "Technical Review",
			items: [item5]
		},
		"do": {
			title: "Deployment",
			items: [item6]
		}
	})

	const handleDragEnd = ({ destination, source }) => {
		if (!destination) {
			return
		}

		if (destination.index === source.index && destination.droppableId === source.droppableId) {
			return
		}

		const itemCopy = { ...state[source.droppableId].items[source.index] }

		setState(prev => {
			prev = { ...prev }
			prev[source.droppableId].items.splice(source.index, 1)
			prev[destination.droppableId].items.splice(destination.index, 0, itemCopy)
			return prev
		})
	}

	return (
		<div className="jumbotron jumbo-form">
			<h5 className="page-heading">My Task</h5>
			<hr className="hr-line" />
			<div className="text-center center" >
				<div className={styles.BoxArea} id="chart">

					<DragDropContext onDragEnd={handleDragEnd}>
						{_.map(state, (data, key) => {
							return (

								<div className={styles.card}>
									<div className={styles['card-body']}>
										<div className="col-sm">
											<h6>{data.title}</h6>
										</div>

										<div key={key} className={styles['card-body1']}>

											<Droppable droppableId={key}>
												{(provided) => {
													
													return (
														<div ref={provided.innerRef}
															{...provided.droppableProps}
															className={styles['card-body2']} >

															{data.items.map((el, index) => {
																return (
																	<Draggable key={el.id} index={index} draggableId={el.id}>
																		{(provided) => {
																			
																			return (
																				<div className={styles.SubBox}
																					ref={provided.innerRef}
																					{...provided.draggableProps}
																					{...provided.dragHandleProps} >
																					<div className={styles.TaskContent}>
																			    	<div>{el.name}</div>
																					<div>{el.name2}</div>
																			    	<div>{el.name3}</div>
																					</div>
																				</div>
																			)
																		}
																		}
																	</Draggable>
																)
															})}
															{provided.placeholder}
														</div>
													)
												}}
											</Droppable>
										</div>
									</div>
								</div>
							)
						})}
					</DragDropContext>
				</div>
			</div>
		</div>
	)
}
export default Task;
